"use client";
import { terapeutas } from "@/content/data";
import { Iterapeuta } from "@/types/global";
import { useParams } from "next/navigation";
import { useEffect, useState } from "react";

const Terapeuta = async () => {
   const param = useParams();
   const [terapeuta, setTerapeuta] = useState<Iterapeuta | null | undefined>(null);

   function getTerapeuta(id: string) {
      setTerapeuta(terapeutas.find((v) => v.id === id));
   }

   useEffect(() => {
      getTerapeuta(`${param.terapeuta}`);
   }, []);
   return <div>Conteudo</div>;
};
export default Terapeuta;
